/*! ===============
Start Home
==================*/
.home {
   height: calc(100vh - 124.47px);
   overflow: hidden;
   .container {
      height: 100%;
      position: relative;
      .layer {
         display: flex;
         justify-content: center;
         height: 100vh;
         margin-left: 50px;
         background-color: #161616;
         color: var(--secColor);
         .text {
            padding-top: 60px;
            h5 {
               color: #E1E1E1;
               font-weight: 400;
            }
            h2 {
               font-weight: 500;
               font-size: 2.5rem;
               letter-spacing: 2px;
            }
            span {
               color: var(--mainColor);
            }
            p {
               color: #E1E1E1;
               font-weight: 300;
               margin: 15px 0;
            }
            .order {
               display: flex;
               justify-content: space-between;
               align-items: center;
               padding-top: 25px;
               button {
                  background-color: var(--mainColor);
                  color: var(--bgColor);
                  text-transform: capitalize;
                  font-size: 1rem;
                  font-weight: 700;
                  padding: 5px 15px;
                  border-radius: 4px;
                  transition: var(--transitionDuration);
                  &:hover {
                     background-color: #d8ac4c;
                  }
               }
               a {
                  color: var(--mainColor);
                  text-decoration: underline;
                  transition: var(--transitionDuration);
                  &:hover {
                     color: var(--secColor);
                  }
               }
            }
         }
      }
      .image {
         position: relative;
         top: 8%;
         img {
            max-width: 100%;
            height: auto;
         }
      }
   }
}

/*! ===============
End Home 
==================*/