/*! ===============
Start Special Menu 
==================*/
.special-menu {
   background-image: url(/imgs/menu-bg.jpg);
   background-repeat: no-repeat; 
   background-size: cover;
   background-attachment: scroll !important;
   padding: var(--sectionPadding);
   position: relative;
   &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 0;
      background-color: rgba($color: #000000, $alpha: 0.6);
   }
   .heading {
      p {
         margin: 15px auto;
         max-width: 50%;
         text-transform: none;
         color: var(--lGray);
      }
   }
   .container {
      position: relative;
      z-index: 1;
      .boxes {
         background-color: transparent;
         justify-content: space-evenly;
         .box {
            flex: 0 0 calc(33.33% - 60px);
            max-width: calc(33.33% - 60px);
            margin: 0;
            background-color: transparent;
            padding: 15px;
            border: 3px solid #cfac61;
            border-radius: 25px;
            @media (max-width: 992px) {
               flex: 0 0 calc(50% - 60px);
               max-width: calc(50% - 60px);
            }
            .inner-box {
               background-color: var(--secColor);
               text-align: center;
               padding: 5px;
               border-radius: 10px;
               img {
                  max-width: 100%;
                  border-radius: 10px;
               }
               h4 {
                  font-size: 1.4rem;
                  color: #5A564C;
                  display: inline-block;
                  padding: 7px 0;
                  border-bottom: 4px solid #cfac61;
               }
               p {
                  color: var(--gray);
                  text-transform: none;
                  margin: 10px 0;
               }
               .order {
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  padding: 15px 10px;
                  .price {
                     color: #cfac61;
                     font-size: 2.1rem;
                     font-weight: 500;
                  }
                  button {
                     background-color: var(--bgColor);
                     padding: 12px;
                     color: #cfac61;
                     font-size: 0.9rem;
                     font-weight: 600;
                     text-transform: capitalize;
                  }
               }
            }
         }
      }
   }
}
/*! ===============
End Special Menu
==================*/