/*! ===============
Start About 
==================*/
.about {
   padding: var(--sectionPaddin) 0;
   .container {
      align-items: center;
      .image {
         padding: 0;
         @media (max-width: 992px) {
            width: 40vw;
            margin: auto;
         }
         img {
            max-width: 100%;
            height: auto;   
         }
      }
      .text {
         padding: 0;
         margin: auto;
         h3 {
            font-size: 1.8rem;
            color: var(--mainColor);
            text-align: center;
            @media (max-width: 520px) {
               font-size: 1.4rem;
            }
         }
         h5 {
            color: var(--lGray);
            text-align: center;
            margin-bottom: 15px;
            @media (max-width: 520px) {
               font-size: 0.7rem;
            }
         }
         p {
            color: var(--secColor);
            font-size: 1.3rem;
            margin: 10px 0;
            @media (max-width: 767px) {
               font-size: 1.1rem;
            }
            @media (max-width: 520px) {
               font-size: 0.9rem;
               margin: 5px 0;
            }
            span {
               font-size: 2.8rem;
               line-height: 1;
               color: var(--mainColor);
               @media (max-width: 520px) {
                  font-size: 2.1rem;
               }
            }
         }
         .txt {
            margin: 10px 0 20px;
            p {
               color: var(--lGray);
               font-size: 1rem;
               @media (max-width: 767px) {
                  max-width: 85%;
               }
               @media (max-width: 520px) {
                  font-size: 0.8rem;
               }
            }
         }
      }
      .founder {
         display: flex;
         justify-content: space-between;
         align-items: center;
         .info {
            display: flex;
            align-items: center;
            img {
               @media (max-width: 520px) {
                  max-width: 60px;
                  height: auto;
               }
            }
            .name {
               margin-left: 15px;
               @media (max-width: 520px) {
                  margin-left: 10px;
               }
               h5 {
                  margin: 0;
                  line-height: 1;
                  font-size: 1.6rem;
                  font-weight: 400;
                  color: var(--secColor);
                  @media (max-width: 767px) {
                     font-size: 1.2rem;
                  }
                  @media (max-width: 520px) {
                     font-size: 1rem;
                  }
               }
               p {
                  color: var(--mainColor);
                  padding: 0;
                  margin: 0;
                  @media (max-width: 767px) {
                     font-size: 0.9rem;
                  }
               }
            }
         }
         .signature {
            font-size: 2.4rem;
            color: var(--lGray);
            font-family: 'Sacramento', cursive;
            @media (max-width: 767px) {
               font-size: 2rem;
            }
            @media (max-width: 520px) {
               font-size: 1.2rem;
            }
         }
      }
   }
}
/*! ===============
End About
==================*/