
/*! ===============
Start Header 
==================*/
header {
   .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      ul {
         display: flex;
      }
   }
   .contact-info-bar {
      background-color: rgba($color: #000000, $alpha: 0.2);
      border-bottom: 1px solid var(--secColor);
      @media (max-width: 767px) {
         display: none;
      }
      .container {
         display: flex;
         justify-content: space-between;
         padding: 10px 0;
         li {
            font-size: 0.9rem;
            font-weight: 300;
            color: var(--secColor);
         }
         ul:first-of-type {
            gap: 20px;
            li:last-of-type {
               color: var(--mainColor);
            }
            a {
               color: var(--secColor);
               transition-duration: var(--transitionDuration);
               &:hover {
                  color: var(--mainColor);
               }
            }
         }
         .social-contact {
            gap: 10px;
            i {
               border: 2px solid var(--mainColor);
               border-radius: 50%;
               padding: 4px;
               transition-duration: var(--transitionDuration);
            }
            a {
               color: var(--mainColor);
            }
            a:hover i {
               color: var(--secColor); 
               border-color: var(--secColor);
            }
         }

      } 
   }
   > .container{
      background-color: var(--bgColor);
      padding: 15px;
      border: 1px solid var(--mainColor);
      border-top: 0;
      border-left: 0;
      border-right: 0;
      position: relative;
      .logo {
         color: var(--mainColor);
         font-size: 1.8rem;
      }
      nav {
         text-transform: uppercase;
         .menu-btn {
            position: absolute;
            right: 15px;
            top: 50%;
            transform: translateY(-50%);
            display: flex;
            flex-direction: column;
            gap: 5px;
            cursor: pointer;
            padding: 12px 10px;
            border: 1px solid var(--mainColor);
            z-index: 1;
            transition-duration: var(--transitionDuration);
            display: none;
            &:hover {
               gap: 7px;
            }
            @media (max-width: 992px) {
               display: flex;
            }
            span {
               height: 2px;
               width: 28px;
               border-radius: 4px;
               background-color: var(--secColor);
            }
         }
         .layer {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 998;
            background-color: rgba($color: #000000, $alpha: 0.8);
            display: none;
            i {
               position: absolute;
               right: 10%;
               top: 10%;
               color: var(--mainColor);
               font-size: 2rem;
               cursor: pointer;
            }
         }
         ul {
            gap: 22px;
            @media (max-width: 992px) {
               display: none;
            }
            a {
               color: var(--secColor);
               transition: var(--transitionDuration);
               &:hover {
                  color: var(--mainColor);
               }
            }
         }
         .col-ul {
            display: flex;
            flex-direction: column;
            position: absolute;
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
            margin-top: 30px;
            z-index: 2222;
            @media (max-width: 767px) {
               margin-top: 55px;
            }
            li {
               font-size: 1.5rem;
               letter-spacing: 1px;
            }
         }
      }
   }
   .fixedNav {
      position: fixed;
      top: 0;
      right: 0;
      left: 0;
      z-index: 3000;
   }
}
/*! ===============
End Header 
==================*/